<template>
	<view>
		<image class="totop" @click="toTop" src="./totop.png" mode="widthFix"></image>
	</view>
</template>

<script>
	export default {
		name: "totop",
		methods: {
			toTop() {
				uni.pageScrollTo({
					scrollTop: 0
				})
			}
		}
	}
</script>

<style>
	.totop {
		position: fixed;
		z-index: 999999;
		color: #fff;
		width: 100rpx;
		height: 100rpx;
		bottom:calc(var(--window-bottom) + 20px);
		right: 40rpx;
	}
</style>
